import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { UserVisitorComponent } from './user-visitor.component';
import { AddVisitorComponent } from './components/create-visitor/add-visitor/add-visitor.component';
import { CreateVisitorComponent } from './components/create-visitor/create-visitor.component';
import { MyVisitorComponent } from './components/my-visitor/my-visitor.component';

const routes: Routes = [

	{ path: '', redirectTo: 'my-visitor', pathMatch: 'full'},
	{ path: 'my-visitor', component: MyVisitorComponent},
	{ path: 'create-visitor', component: CreateVisitorComponent },
	{ path: 'edit-visitor/:id', component: CreateVisitorComponent },
	{ path: '**', redirectTo: 'my-visitor', pathMatch: 'full'}
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserVisitorRoutingModule { }
