import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-create-visitor',
  templateUrl: './create-visitor.component.html',
  styleUrls: ['./create-visitor.component.scss']
})
export class CreateVisitorComponent implements OnInit {

  visitorArray:any;
  visitorCount:number = 1;
  isEditVisitor:boolean = false;


  constructor(
  	private router: Router,
    private route: ActivatedRoute
  ) { }


  
  addVisitor() {
    this.visitorCount++;
    this.visitorArray =  Array(this.visitorCount).fill(0).map((x,i)=>i);
  }

  getVisitorArray(event){
    this.visitorArray = event;
    this.visitorCount = this.visitorArray.length;
  }

  isMobileView() {
    return window.innerWidth <= 576;
  }

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.isEditVisitor = true;
    }

    this.visitorArray =  Array(this.visitorCount).fill(0).map((x,i)=>i);

  	

  }

}
