import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from '../../../../../api/services/user.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { LookupService } from '../../../../../api/services/lookup.service';
import { ApartmentService } from '../../../../../api/services/apartment.service';
import { VisitorService } from '../../../../../api/services/visitor.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';

@Component({
  selector: 'app-add-visitor',
  templateUrl: './add-visitor.component.html',
  styleUrls: ['./add-visitor.component.scss']
})
export class AddVisitorComponent implements OnInit {

  isEditVisitor:boolean = false;

  isVisitorSubmitted:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  visitor:any;

  visitByData:any;
  visitTypeId:string = "";

  apartmentUnitId:number;

  @Input ('index') index;
  @Input ('array') visitorArray;

  @Output() public outputParams = new EventEmitter();

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
  ) { }


  isArrayOne(){
  	return this.visitorArray.length > 1 ? true : false;
  }

  deleteVisitor(){
  	this.visitorArray.splice(this.index, 1);
    this.outputParams.emit(this.visitorArray);
  }

  submitAddVisitorForm(form) {

  	this.isVisitorSubmitted = true;
    this.isError = false;

    if(!this.isEditVisitor) {

      let visitorDetails = {
    		"apartmentUnitId": this.apartmentUnitId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "expectedVisitorName": this.visitor.expectedVisitorName,
        "expectedVisitorCount": this.visitor.expectedVisitorCount,
        "expectedVisitorPhone": this.visitor.expectedVisitorPhone.toString(),
        "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
        "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
        "expectedDuration": this.visitor.expectedDuration,
        "meetingPersonId": 1,
        "visitTypeId": parseInt(this.visitTypeId),
        "expectedVisitorsOrg": "string",
        "entryGateId": null,
        "exitGateId": null,
        "purpose": "string",
        "enteredBy": parseInt(this.cookieService.get('userId')),
        "isActive": true,
        "insertedBy":parseInt(this.cookieService.get('userId')),
        "insertedOn": "2019-11-18T15:27:41.030Z",
        "updatedBy": 0,
        "updatedOn": "2019-11-18T15:27:41.030Z"
      }

    
  //moment(this.visitor.visitorInTime).hour() + ':' + moment(this.visitor.visitorInTime).minutes()    

    this.visitorService.addExpectedVisitor(visitorDetails).subscribe((res:any) => {
          
        if(res.message) {
          this.isVisitorSubmitted = true;
          this.sharedService.setAlertMessage("Visitor added successfully");
          this.router.navigate(['user/visitor/my-visitor']);
        }
        else {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
      error => {
        
    		this.isVisitorSubmitted = false;
    		this.isError = true;
    		this.errorMessage = error;

      });

    }	

    else {

      let visitorDetails = {
        
        "expectedVisitorId": this.route.params['value'].id,
        "apartmentUnitId": this.apartmentUnitId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "expectedVisitorName": this.visitor.expectedVisitorName,
        "expectedVisitorCount": this.visitor.expectedVisitorCount,
        "expectedVisitorPhone": this.visitor.expectedVisitorPhone.toString(),
        "expectedVisitorInTime": this.visitor.expectedVisitorInTime,
        "expectedVisitorOutTime": this.visitor.expectedVisitorOutTime,
        "expectedDuration": this.visitor.expectedDuration,
        "meetingPersonId": 1,
        "visitTypeId": parseInt(this.visitTypeId),
        "expectedVisitorsOrg": "string",
        "entryGateId": null,
        "exitGateId": null,
        "purpose": "string",
        "enteredBy": parseInt(this.cookieService.get('userId')),
        "isActive": true,
        "insertedBy":0,
        "insertedOn": "2019-11-18T15:27:41.030Z",
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": "2019-11-18T15:27:41.030Z"
      }


      this.visitorService.updateExpectedVisitor(visitorDetails).subscribe((res:any) => {
          
        if(res.message) {
          this.isVisitorSubmitted = true;
          this.sharedService.setAlertMessage("Visitor updated successfully");
          this.router.navigate(['user/visitor/my-visitor']);
        }
        else {

          this.isVisitorSubmitted = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
      error => {
        
        this.isVisitorSubmitted = false;
        this.isError = true;
        this.errorMessage = error;

      });



    }

  }

  ngOnInit() {

  	this.visitor = {};

    if(this.route.params['value'].id != undefined){
      this.isEditVisitor = true;
    }

    if(this.route.params['value'].id != undefined) {

      this.isEditVisitor = true;
      
      this.visitorService.getExpectedVisitorById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.visitor = res[0];
        this.visitTypeId = this.visitor.visitTypeId;

      },
      error => {
        
      }
      );

    }

    //visit type
    this.lookupService.getLookupValueByLookupTypeId(15).subscribe((res:any) => {
          
      this.visitByData = res;

    },
    error => {
      
    }
    );

	this.apartmentService.getApartmentBlockUnitByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

		this.apartmentUnitId = res[0].apartmentBlockUnitId;

	},
	error => {

	}
	);

  }


}
